<template>
    <div class="container flex-grow-1 container-p-y">
        <h4 class="fw-bold py-3 mb-4">
            <span class="text-muted fw-light text-center">SAIV{{store.state.USUARIO.oficina}} /</span> Ludoteca
        </h4>
        <!-- Bordered Table -->
        <div class="card">
            <div class="card-body">                
                <TableVue ref="tablaCasosRef"
                    :columnas="columnas" 
                    :url="urlTabla" 
                    @handleChangedLoading="handleChangedLoading"
                    @handleChangedData="handleChangedData"
                >
                    <template v-slot:row2>
                        <div class="col-md-4">
                            <div class="input-group border-primary flex justify-content-end">
                                <span class="input-group-text border-primary bg-primary text-white text-center border-white" for="tipo-caso">Tipo Caso<i class=' bx bxs-label' ></i></span>
                                <select class="form-select bg-primary text-white" id="tipo-caso" v-model="tipoCaso" @change="tablaCasosRef.handleChangeFiltro(tipoCaso)" >                                     
                                    <template v-for="item in ['Todos', 'Denuncia', 'Sin Denuncia']" :key="item" >
                                        <option class=" bg-primary text-white" :value="item">{{item}}</option>
                                    </template>
                                </select>
                            </div>   
                        </div>
                    </template>
                    <template v-slot:button >
                        <button class="btn btn-primary m-1 justify-items-start" 
                            @click="handleClickNuevo" :disabled="loading">
                            <i class="bx bx-plus" style="font-size:20px"/> Nuevo
                        </button> 
                    </template>  
                </TableVue> 
            </div>
        </div>
        <ModalVue ref="modalLudotecaRef" @handleActualizarDatosTabla="handleActualizarDatos">
            <template v-slot:cardBody>
                <Form ref="formLudotecaRef"                    
                    :loading="loading"
                    :showVer="showVer ?? false"
                    v-show="intercambiarForm"
                />
                <Transition name="fade">
                    <FormCasoVue :showVer="true" v-show="!intercambiarForm" ref="formCasoRef">
                        <template v-slot:codigo-caso>
                            <div class="row pb-2 pt-2 border border-2 border-primary rounded ">
                                <div class="col-md-6">
                                    <label class=" fw-semibold d-block" for="caso-tipo-hecho"> Codigo de caso</label>
                                    <div class="col-md">
                                        <vSelectPaginationVue
                                            :url="'saiv/casos/indexSelect'"
                                            v-model="codigoCaso"
                                        />
                                    </div>
                                </div>
                            </div>
                        </template>
                        <template v-slot:nuevo-button>
                            <button @click="handleChangedIntercambiarForm" type="button" class="btn btn-secondary m-1" :disabled="loading" v-if="!showVer ?? true">
                                <i class='bx bx-transfer-alt bx-sm'></i> Volver al Seguimiento Jurídico
                            </button>
                        </template>
                    </FormCasoVue>
                </Transition>
            </template>
        </ModalVue>
    </div>
</template>
<script>
import { defineComponent, ref } from 'vue';
// Servicios
import store      from '@/store/index'
import servicio   from '@/services/crud'

// Components
import FormCasoVue  from '@/components/casos/FormCaso.vue';
import ModalVue     from '@/components/Modal.vue'
import TableVue     from '@/components/layout/Table.vue'
import Form         from '@/components/ludoteca/Form.vue'
const columnas = [
    {nombre:"Codigo", class:"text-center bg-primary text-white fw-bold",    key:"codigo",   style:"", },
];

const urlTabla  = 'saiv/seguimiento/juridico/index';

export default defineComponent({
    watch:{
        codigoCaso: async function(nuevo){
            if(typeof nuevo === 'object' && nuevo!== null)
                await this.cargarCaso(nuevo);
            else 
                this.resetFormCaso();
        }
    },
    setup(props) {
        // Variables
        const loading   = ref(false);
        const showVer   = ref(false);
        const data      = ref([]);
        const tipoCaso  = ref('Todos');
        const modalLudotecaRef = ref(null);
        const intercambiarForm = ref(true);
        const codigoCaso = ref(null);
        // Metodos
        const handleChangedLoading = _loading => {
            loading.value = _loading;
        };

        const handleChangedData = _data => {
            data.value = _data
        };

        const handleClickNuevo = () => {
            showVer.value = null;
            // formSeguimientoJuridicoRef.value.resetForm();
            modalLudotecaRef.value.showModal();
        };

        return {
            // Ref
            modalLudotecaRef,
            // Variables
            intercambiarForm,
            codigoCaso,
            urlTabla,
            columnas,
            tipoCaso,
            showVer,
            loading,
            store,
            // Metodos
            handleChangedLoading,
            handleChangedData,
            handleClickNuevo,
        }
    },
    components:{
        FormCasoVue,
        ModalVue,
        TableVue,
        Form,
    }
});
</script>